import * as React from "react"
import styled from "styled-components"
import { useState, useEffect } from "react"

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  min-height: 100vh;
  position: relative;
`


// markup
const IndexPage = () => {

  const [stat, setStat] = useState("we're in the backend");
  const isBrowser = () => typeof window !== 'undefined';
  const xd = ()=>{
    setStat("we're in the frontend")
  }

  useEffect(()=>{
    xd()
  },[isBrowser()])

  
  
  return (
    <Wrapper>
    {
      stat
    }
    </Wrapper>
  )
}

export default IndexPage
